
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data: function () {
return {}
},
methods: {
loginOut: function () {
cookie.remove(this.consts.ticketKey);
window.vm.$emit(this.consts.loginOutEvent);
}
},
created: function () {
}
}



