
import formValidate from '../../common/formValidate'
import moduleList from '../../common/moduleList'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
mixins: [formValidate, moduleList],
data: function () {
return {
status: 0,
pop: false,
modalLoading: false,
fieldSet: {
desc: '',
code: ''
},
selections: {
code: [{code: 100, name: '倾向于谁做'}, {code: 200, name: '性别'}, {code: 300, name: '项目周期'},{code:400,name:'工作经验'}]
},
codeMap:{
100:'倾向于谁做',
200:'性别',
300:'项目周期',
400:'工作经验'
},
list: {
columns: [
{title: '描述', key: 'desc'},
{title: '类型', key: 'code',render:(h,params)=>{
return h('span',this.codeMap[params.row.code])
}},
{
title: '操作',
key: 'action',
render: (h, params) => {
return h('div', [
h('Button', {
props: {
type: 'text',
size: 'small'
},
on: {
click: (e) => {
this.remove(params.row, e)
}
}
}, [h('Icon', {props: {type: 'trash-a'},class:{'margin-right-10':true}}), '删除']),
h('Button', {
props: {
type: 'text',
size: 'small'
},
on: {
click: (e) => {
this.edit(params.row, e)
}
}
}, [h('Icon', {props: {type: 'edit'},class:{'margin-right-10':true}}), '修改'])
]);
}
}
],
url: 'admin/queryDataDictionary',
}
}
},
methods: {
add: function () {
this.reset()
this.pop = true
},
edit:function(data){
this.reset()
this.setValues(data)
this.pop = true
},
addSubmit: function () {
if (this.validate(true)) {
var params = this.getValues()
this.modalLoading = true
this.$http.post(this.url('admin/addDataDictionary'), params).then(this.rspHandler(() => {
this.modalLoading = false
this.pop=false
this.refreshList(1)
}, () => {
this.modalLoading = false
}))
}
},
reset: function () {
this.fieldSet = {
desc: '',
code: ''
}
},
remove: function (data) {
this.$Modal.confirm({
title: '删除',
content: '<p>确认是否删除！</p>',
onOk: () => {
this.$http.get(this.url('admin/failDataDictionary'), {params:{id:data.id}}).then(this.rspHandler(() => {
this.refreshList(1)
}))
}
});
}
},
created: function () {
this.initList(this.list)
this.$on(this.consts.loadedEvent, function () {
this.refreshList(1)
})
}
}



