
import formValidate from '../../common/formValidate'
import moduleList from '../../common/moduleList'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
mixins: [formValidate, moduleList],
data: function () {
return {
status: 0,
pop: false,
modalLoading: false,
fieldSet: {
userName: '',
passWord:''
},
rule:{
userName: {
label: '账号',
required: true
},
passWord: {
label: '密码',
required: true
}
},
list: {
columns: [
{title: '账号', key: 'userName'},
{
title: '更新时间', key: 'updatedAt', render: (h, params) => {
return h('span', {}, dateFormat(params.row.updatedAt, 'YYYY-MM-DD'));
}
},
{
title: '操作',
key: 'action',
render: (h, params) => {
return h('div', [
h('Button', {
props: {
type: 'text',
size: 'small'
},
on: {
click: (e) => {
this.edit(params.row, e)
}
}
}, [h('Icon', {props: {type: 'edit'},class:{'margin-right-10':true}}), '修改'])
]);
}
}
],
url: 'admin/getUsers',
}
}
},
methods: {
add: function () {
this.reset()
this.pop = true
},
edit:function(data){
this.reset()
this.setValues(data)
this.fieldSet.passWord = '';
this.pop = true
},
submit: function () {
if (this.validate(true)) {
var params = this.getValues()
this.modalLoading = true
this.$http.post(this.url('admin/addUser'), params).then(this.rspHandler(() => {
this.modalLoading = false
this.pop=false
this.refreshList(1)
}))
}
},
reset: function () {
this.fieldSet = {
userName: '',
passWord:''
}
},
remove: function (data) {
this.$Modal.confirm({
title: '删除',
content: '<p>确认是否删除！</p>',
onOk: () => {
console.log(this)
this.$http.get(this.url('admin/deleteUser/'+data.id)).then(this.rspHandler(() => {
this.refreshList(1)
}))
}
});
}
},
created: function () {
this.initList(this.list)
this.$on(this.consts.loadedEvent, function () {
this.refreshList(1)
})
}
}



