
import formValidate from '../../common/formValidate'
import moduleList from '../../common/moduleList'
import dateFormat from 'vux/src/tools/date/format.js'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
mixins: [formValidate, moduleList],
data: function () {
return {
status: 0,
pop: false,
modalLoading: false,
fieldSet: {
caseIndex: 1,
caseShow: '1',
caseUrl: '',
caseText: '',
businessId: ''
},
rule:{
caseIndex:{
label:'序列',
required:true,
number:true,
digits:true
}
},
list: {
columns: [
{title: '序列', key: 'caseIndex'},
{
title: '操作',
key: 'action',
render: (h, params) => {
return h('div', {}, [
h('Button', {
props: {
type: 'text',
size: 'small'
},
on: {
click: (e) => {
this.remove(params.row, e)
}
}
}, [h('Icon', {props: {type: 'trash-a'}, class: {'margin-right-10': true}}), '删除']),
h('Button', {
props: {
type: 'text',
size: 'small'
},
on: {
click: (e) => {
this.edit(params.row, e)
}
}
}, [h('Icon', {props: {type: 'edit'}, class: {'margin-right-10': true}}), '修改'])
]);
}
}
],
url: 'business/queryCaseBases',
params:{
id:''
},
page:0
}
}
},
methods: {
add: function () {
this.reset()
this.pop = true
},
edit: function (data) {
this.reset()
this.setValues(data)
this.pop = true
},
submit: function () {
if (this.validate(true)) {
var params = this.getValues()
this.modalLoading = true
this.$http.post(this.url('admin/addCaseBases'), params).then(this.rspHandler(() => {
this.modalLoading = false
this.pop = false
this.refreshList()
}, () => {
this.modalLoading = false
}))
}
},
reset: function () {
this.fieldSet = {
caseIndex: 1,
caseShow: 1,
caseUrl: '',
caseText: '',
businessId: ''
}
if(this.$route.params.id){
this.fieldSet.businessId = this.$route.params.id
}
},
remove: function (data) {
this.$Modal.confirm({
title: '删除',
content: '<p>确认是否删除！</p>',
onOk: () => {
this.$http.get(this.url('admin/deleteCaseBase'),{params:{id:data.id}}).then(this.rspHandler((data)=>{
this.refreshList()
}))
}
});
},
},
created: function () {
this.initList(this.list)
if(this.$route.params.id){
this.list.params.id = this.$route.params.id
}
this.reset()
this.$on(this.consts.loadedEvent, function () {
this.refreshList()
})
}
}



